@import url(https://fonts.googleapis.com/css2?family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&family=Kode+Mono:wght@400..700&display=swap);
/* Fonts */


/* Reset */
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html{
  font-size: 62.5%;
}

body{
  font-size: 1.6rem;
  font-family: var(--font2);
}
body.light{
  background: var(--bg-gradient);
}
body.dark{
  background: black;
}

/* Variables */
:root{
  --bg-gradient: linear-gradient(to right, #000000, #c89116);
  --gradient: linear-gradient(to left, #000000, #c89116);

  --yellow: #c89116;
  --lighter-yellow: #faba667a;
  --black: #0000;
  --white: white;  

  --shadow-1: 0 0 1rem #c89116;
  --shadow-2: 0 0 5rem orange;
  --shadow-3: 0.1rem 0.1rem 0.5rem rgba(0, 0, 0, 0.678);

  --font1: 'kode mono', sans-serif;
  --font2: 'DM Sans';
}

/* Global */

body{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: auto;
  width: 100%;
}
.menu-icon{
    display: flex;
    flex-direction: column;
    grid-gap: 0.3rem;
    gap: 0.3rem;
    z-index: 1001;
    position: absolute;
    left: 3rem;
    top: 1.3rem;
}
.mobile-menu-nav{
    position: fixed;
    left: 0;
    top: 0;
    padding: 5em 2rem;
    height: 200dvh;
    width: 60dvw;
    background: rgba(20, 17, 17, 0.993);
    box-shadow: var(--shadow-3);
    z-index: 1000;
    transition: transform 0.3s ease;
    transform: translateX(-100%);
}
.mobile-menu-nav.open{
    transform: translateX(0);
}
.mobile-menu-nav ul{
    display: flex;
    flex-direction: column;
    grid-gap: 3rem;
    gap: 3rem;
}
.mobile-menu-nav ul li{
    list-style: none;
}
a{
    font-size: 1.8rem;
}
li a{
    font-family: var(--font2);
    text-transform: uppercase;
    transition: 0.3s ease-in-out;
}
li a:hover{
    border-bottom: 1px solid var(--white);
}
.desktop-menu-nav{
    display: none;
}

@media (min-width: 768px){
    .menu-icon, .mobile-menu-nav{
        display: none;
    }
    .desktop-menu-nav{
        display: flex;
        position: fixed;
        top: 5rem;
        left: 4rem;
    }
    .desktop-menu-nav ul{
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        grid-gap: 5rem;
        gap: 5rem;
    }
    .desktop-menu-nav ul li{
        list-style: none;
    }
    li a{
        font-size: 1.2rem;
    }

}
.header-container{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    grid-gap: 15%;
    gap: 15%;
    position: fixed;
    width: 110dvw;
    padding: 1rem 0rem;
   /*  background: rgba(114, 99, 99, 0.171); */
    -webkit-backdrop-filter: blur(1rem);
            backdrop-filter: blur(1rem);
    z-index: 1000;

}
.header-container p{
    font-size: 1.3rem;
    color: var(--white);
    text-transform: uppercase;
}
.header-container p span{
    color: var(--yellow);
    font-weight: bolder;
}


.header-container p svg {
    font-size: 1.8rem;
    position: fixed;
    top: 1rem;
}
.home-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.main-home-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.matheus-image {
    width: 15rem;
    border-radius: 100%;
    margin: 10rem 0rem 2rem 0rem;
    border: 0.5rem solid rgb(245, 220, 141);
    box-shadow: var(--shadow-2), inset var(--shadow-1);
    animation: subirDescer 2.5s ease-in-out infinite;
}

@keyframes subirDescer {

    0%,
    100% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-20px);
    }
}


/* greeting */
.greeting {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.greeting h1 {
    font-size: 1.5rem;
    font-family: var(--font1);
    width: 85%;
    text-indent: 0%;
    margin-bottom: 1rem;
    color: var(--white);
    opacity: 0.8;
}

.greeting h2 {
    font-size: 3.5rem;
    margin-bottom: -0.5rem;
    color: var(--white);
    font-family: var(--font1);
}

.greeting h3 {
    font-size: 1.5rem;
    font-weight: 500;
    margin-bottom: 8rem;
    color: var(--white);
    opacity: 0.6;
    text-transform: uppercase;
}

/* Links */
.home-links {
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
}

.home-links a {
    padding: 0.5rem 9rem;
    border-radius: 0.7rem;
    text-transform: uppercase;
    font-weight: 550;
    transition: 0.3s ease-in-out;
    text-align: center;
}

.home-links a:hover {
    opacity: 0.5;
}

.fale-comigo-link {
    background: var(--yellow);
    border: 0.2rem solid var(--yellow);
    box-shadow: var(--shadow-3);
    color: var(--white);
}

.cv-link {
    background: transparent;
    border: 0.2rem solid var(--lighter-yellow);
    box-shadow: var(--shadow-1);
    text-shadow: var(--shadow-1);
    color: #fdd785fb;
}


/* Social medias */
.social-medias {
    display: flex;
    flex-direction: row;
    grid-gap: 2rem;
    gap: 2rem;
    margin-top: 3rem;
}

.social-medias a {
    color: var(--white);
    box-shadow: var(--shadow-2);
    border-radius: 100%;
    transition: transform 0.3s ease;
}

.social-medias a:hover {
    transform: scale(1.2);
}

/* ********* RESPONSIVE ********* */


@media(min-width: 768px)  {
    .main-home-container{
        flex-direction: row;
        justify-content: space-around;
        width: 100%;
    }
    .matheus-image {
        width: 26rem;
    }

    .greeting h1 {

        font-size: 2rem;
        margin-top: 15rem;
    }

    .greeting h2 {
        font-size: 5rem;
    }

    .greeting h3 {
        font-size: 2.3rem;
    }

    /* Links */
    
    .home-links a {
        padding: 1rem 3rem;
        font-size: 1.9rem;
        text-align: center;
    }
}

