  }
.projects{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.projects-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 0rem 6rem;
    grid-gap: 5rem;
    gap: 5rem;
    opacity: 0;
    transform: translateY(10rem);
    transition: opacity 3s ease, transform 2s ease;

}
.projects-container.fade-in{
    opacity: 1; 
    transform: translateY(0);
}
.projects h1{
    font-family: var(--font1);
    margin-bottom: 4rem;
